.table {
    margin: 20px auto;
    border-collapse: separate;
    width: 100%;
    height: 500px;
    opacity: 0.5;
    border-radius: 10px;
}

th,
td {
    text-align: center;
    margin: 10px;
    border: 1px solid #000;
    background-color: #fff;
    opacity: 1;
}

td {
    width: 200px;
}